import React from "react";

import "./CharacterItem.scss";

const CharacterItem: React.FC<{
  id: string;
  imageUrl: string;
  isSelected?: boolean;
  onSelect(id: string): void;
}> = ({ imageUrl, isSelected = false, onSelect, id }) => {
  function handleOnSelect() {
    return onSelect(id);
  }

  //Inline style for component
  const componentStyle = {
    border: isSelected ? "2px solid white" : " 0px",
  } as React.CSSProperties;

  return (
    <div className="CharacterItem" onClick={() => handleOnSelect()}>
      <img src={imageUrl} style={componentStyle} />
    </div>
  );
};

export default CharacterItem;
