import React, { RefObject, useEffect, useRef, useState } from "react";
import { Redirect, useHistory, useParams } from "react-router";

import "./ChapterDetail.scss";
import API_DATA from "../../services/chapter_data";
import ChapterDetailHeader from "./components/Header";
import useEventListener from "../../utils/useEventListener";

import ArrowFoward from "@material-ui/icons/ArrowForwardIosRounded";

interface ChapterDetailParams {
  id: string;
}

const ToTopButton: React.FC<{ hidden?: boolean }> = ({ hidden = false }) => {
  const className = hidden ? "ToTopButton fade-out" : "ToTopButton fade-in";

  function toTop() {
    const element = document.getElementById("chapter-detail");
    element?.scrollTo(0, 0);
  }

  return (
    <div className={className} onClick={() => toTop()}>
      <ArrowFoward className="arrow" />
      Top
    </div>
  );
};

const ChapterDetailScreen: React.FC = () => {
  const history = useHistory();
  const params = useParams<ChapterDetailParams>();
  const id = params.id;

  const chapterImagesResponse = API_DATA.filter(
    (chapters) => chapters.id === id
  );

  const chapterData = chapterImagesResponse[0];

  const [isTop, setTop] = useState(true);

  /** SCROLL LISTENER */
  const chapterDetailReference = useRef<HTMLDivElement>(null);
  const onScroll = (event: Event) => {
    const scroll = document.getElementById("chapter-detail")?.scrollTop;
    if (scroll && scroll > 10) {
      setTop(false);
    } else {
      setTop(true);
    }
  };
  useEventListener("scroll", onScroll, chapterDetailReference);

  if (!chapterImagesResponse.length) {
    return <Redirect to={"/404"} />;
  }

  return (
    <div
      className="ChapterDetail"
      ref={chapterDetailReference}
      id="chapter-detail"
    >
      <ChapterDetailHeader
        previousId={chapterData.previous_chapter_id}
        nextId={chapterData.next_chapter_id}
        title={chapterData.title}
        chapterNumber={chapterData.chapterNumber}
        hidden={!isTop}
      />
      <ToTopButton hidden={isTop} />
      <div className="content">
        {chapterData.data.map((img) => (
          <img src={img} className="content-image" />
        ))}
      </div>
    </div>
  );
};

export default ChapterDetailScreen;
