import React, { useState } from "react";
import { Carousel } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import "./Home.scss";
import { useHistory } from "react-router";

interface CarouselProps {
  slides: Slides[];
}

interface Slides {
  image_url: string;
  caption_title: string;
  caption_description: string;
}

const images = [
  {
    image_url:
      "https://firebasestorage.googleapis.com/v0/b/streamer-website.appspot.com/o/comfy-cartel%2Fcomfy-cartel-001.jpg?alt=media&token=904751bb-7bec-4d3c-8040-dbc5859f86bb",
    caption_title: "The Comfy Cartel",
    caption_description: "Welcome",
  },
  {
    image_url:
      "https://pbs.twimg.com/media/EoVFdvuXYAMmErE?format=jpg&name=large",
    caption_title: "Corpse Husband",
    caption_description:
      "Corpse receives an invitation from a certain someone...",
  },

  {
    image_url:
      "https://firebasestorage.googleapis.com/v0/b/streamer-website.appspot.com/o/comfy-cartel%2Fcomfy-cartel-002.jpg?alt=media&token=aded64d1-0b90-4078-8005-2100f971a091",
    caption_title: "Sykkuno",
    caption_description:
      "Corpse learns that his handler isn’t all that he seems",
  },
  {
    image_url:
      "https://pbs.twimg.com/media/EtSw4YmVkAA9KaE?format=jpg&name=large",
    caption_title: "Peter Park",
    caption_description: "Yakuza Peter",
  },
] as Slides[];

function ControlledCarousel(props: CarouselProps) {
  const { slides = [] } = props;

  const [index, setIndex] = useState(0);
  const handleSelect = (selectedIndex: number, e: any) => {
    setIndex(selectedIndex);
  };

  return (
    <Carousel
      // fade={true} //Causing trouble with router transition animation
      className="carousel"
      activeIndex={index}
      onSelect={handleSelect}
    >
      {slides.map((source) => (
        <Carousel.Item style={{ height: "100vh" }}>
          <img
            style={{
              objectFit: "cover",
              width: "100%",
              height: "100%",
            }}
            className="d-block w-100"
            src={source.image_url}
            alt="First slide"
          />
          <div className="carousel-bg" />
          <Carousel.Caption className="carousel-caption">
            <h3>{source.caption_title}</h3>
            <p>{source.caption_description}</p>
          </Carousel.Caption>
        </Carousel.Item>
      ))}
    </Carousel>
  );
}

const HomeScreen: React.FC = () => {
  const history = useHistory();
  return (
    <div className="Home">
      <ControlledCarousel slides={images} />
      <div className="bg-gradient" />
      <div
        className="read-chapter-button"
        onClick={() => history.push("/chapters")}
      >
        Read Chapters
      </div>
    </div>
  );
};

export default HomeScreen;
