import React from "react";
import { render } from "react-dom";
import { Redirect, useHistory } from "react-router";
import "./ChapterItem.scss";
import API_DATA from "../../../../services/chapter_data";

interface ChapterItemProps {
  thumbnailUrl: string;
  title: string;
  description: string;
  creationTime: Date;

  id: string;
}

const ChapterItem: React.FC<ChapterItemProps> = (ChapterItemProps) => {
  const history = useHistory();

  function handleOnClick() {
    const chapterData = API_DATA.filter(
      (data) => data.id === ChapterItemProps.id
    );

    console.log("Inside length", chapterData);
    if (!chapterData.length) {
      history.push("/404");
    } else {
      history.push(`/chapters/${ChapterItemProps.id}`);
    }
  }

  return (
    <div className="ChapterItem" onClick={() => handleOnClick()}>
      <img src={ChapterItemProps.thumbnailUrl} />
      <div className="content">
        <div>
          <h1>{ChapterItemProps.title}</h1>
          <h2>{ChapterItemProps.description}</h2>
        </div>
        <h3>{ChapterItemProps.creationTime.toDateString()}</h3>
      </div>
    </div>
  );
};

export default ChapterItem;
