import React, { useEffect, useState } from "react";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import {
  CSSTransition,
  SwitchTransition,
  TransitionGroup,
} from "react-transition-group";

import "./Characters.scss";
import CharacterItem from "./components/CharacterItem";

import BG from "../../assets/images/comfy-cartel-001.jpg";

import character_data from "../../services/character_data";

import {
  ArrowDropDownCircle,
  ArrowForwardIosRounded,
} from "@material-ui/icons";
import Social from "./components/Social";

interface Social {
  url: string;
  type: string;
}

interface CharacterProps {
  id: string;
  name: string;
  role: string;
  socials: Social[];
  lore?: string;
  coverImage: string;
  profileImage: string;
  thumbnailProfileImage: string;

  isSelected?: boolean;
}

const responsive = {
  superLargeDesktop: {
    // the naming can be any, depends on you.
    breakpoint: { max: 4000, min: 3000 },
    items: 8,
    partialVisibilityGutter: 10,
  },
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 6,
    partialVisibilityGutter: 10,
  },
  tablet: {
    breakpoint: { max: 1024, min: 464 },
    items: 6,
    partialVisibilityGutter: 10,
  },
  mobile: {
    breakpoint: { max: 464, min: 0 },
    items: 6,
    partialVisibilityGutter: 10,
  },
};

const CharacterScreen: React.FC = () => {
  const [characters, setSelectedCharacters] = useState<CharacterProps[]>(
    character_data
  );

  //To make sure all selected character is set initially to false
  useEffect(() => {
    characters.forEach((char) => (char.isSelected = false));
  }, [characters]);

  const [selectedChar, setSelectedChar] = useState<CharacterProps | null>(null);

  //Handler for rerender character carousel
  function handleSelectCharacter(id: string): void {
    let selectedChar: CharacterProps | null = null;

    const output = characters.map((char) => {
      if (char.id === id) {
        char.isSelected = true;
        selectedChar = char;
      } else {
        char.isSelected = false;
      }
      return char;
    });

    setSelectedCharacters(output);

    setSelectedChar(selectedChar);

    const charContent = document.getElementById("selected-char-content");
    charContent?.scrollTo(0, 0);
  }

  return (
    <div className="Characters">
      {/*  Background images */}

      <div className="background-image">
        <CSSTransition
          key={Date.now()}
          in={!!selectedChar}
          timeout={300}
          classNames="fade"
        >
          <img src={selectedChar?.coverImage || BG} />
        </CSSTransition>

        {/* background gradient */}
        <div className="bg-gradient" />
      </div>

      {/* Selected Container */}
      <div className="character-container">
        <TransitionGroup className="character-container-content">
          <CSSTransition
            key={new Date().toString()}
            timeout={300}
            classNames="fade"
          >
            {selectedChar ? (
              <CSSTransition
                in={!!selectedChar}
                timeout={300}
                classNames="fade"
              >
                {/* Selected Character container */}
                <div className="selected-container">
                  <div className="profile-container">
                    <img src={selectedChar?.profileImage} />
                  </div>
                  <div id="selected-char-content" className="content">
                    <h1 className="username">{selectedChar.name}</h1>
                    <p className="role">{selectedChar.role}</p>

                    <div className="socials-container">
                      {selectedChar.socials.map((social) => (
                        <div className="social">
                          <Social
                            size={40}
                            url={social.url}
                            type={social.type}
                          />
                        </div>
                      ))}
                    </div>

                    <h1 className="lore-title">{`Lore/>`}</h1>
                    <span className="lore-description">
                      {selectedChar.lore}
                    </span>
                  </div>
                </div>
              </CSSTransition>
            ) : (
              // No character selected
              <div className="unselected-container">
                <h1>Select Character</h1>
                <ArrowForwardIosRounded className="arrow" />
              </div>
            )}
          </CSSTransition>
        </TransitionGroup>

        <div className="carousel-parent-container">
          <Carousel
            className="carousel"
            containerClass="carousel-container"
            itemClass="carousel-item"
            draggable={false}
            partialVisible={true}
            infinite
            focusOnSelect={true}
            responsive={responsive}
          >
            {characters.map((char) => (
              <CharacterItem
                id={`${char.id}`}
                isSelected={char.isSelected}
                onSelect={(id) => handleSelectCharacter(id)}
                imageUrl={char.thumbnailProfileImage}
              />
            ))}
          </Carousel>
        </div>
      </div>
    </div>
  );
};

export default CharacterScreen;
