import "./App.scss";

import { Switch, Route, useLocation } from "react-router-dom";
import { CSSTransition, TransitionGroup } from "react-transition-group";

import Navbar from "./common/Navbar/Navbar";
import HomeScreen from "./screens/Home/Home";
import AboutScreen from "./screens/About/About";
import CharacterScreen from "./screens/Characters/Characters";
import ChapterListScreen from "./screens/ChapterList/ChapterList";
import ChapterDetailScreen from "./screens/ChapterDetail/ChapterDetail";
import NotFoundScreen from "./screens/NotFound/NotFound";

const routes = [
  { path: "/", name: "Home", Component: HomeScreen },
  { path: "/about", name: "About", Component: AboutScreen },
];

function App() {
  const location = useLocation();

  return (
    <div className="App">
      <Navbar />

      <TransitionGroup>
        <CSSTransition key={location.key} timeout={300} classNames="app">
          <Switch location={location}>
            <Route path="/" exact component={HomeScreen} />
            <Route path="/chapters/:id" component={ChapterDetailScreen} />
            <Route path="/chapters" exact component={ChapterListScreen} />
            <Route path="/characters" component={CharacterScreen} />
            <Route path="/about" component={AboutScreen} />
            <Route path="/404" component={NotFoundScreen} />
            <Route component={NotFoundScreen} />
          </Switch>
        </CSSTransition>
      </TransitionGroup>
    </div>
  );
}

export default App;
