import React, { useCallback, useEffect, useState } from "react";
import { useHistory } from "react-router";
import Logo from "../../../assets/images/logo-03.png";
import "./Header.scss";

interface HeaderProps {
  hidden?: boolean;
  previousId?: string | null;
  nextId?: string | null;
  title: string;
  chapterNumber: number;
}
const ChapterDetailHeader: React.FC<HeaderProps> = ({
  hidden = false,
  ...props
}) => {
  const className = hidden
    ? "ChapterDetailHeader fade-out"
    : "ChapterDetailHeader fade-in";

  const arrowLeftClassName = props.previousId
    ? "arrow-container"
    : "arrow-container disabled";

  const arrowRightClassName = props.nextId
    ? "arrow-container"
    : "arrow-container disabled";

  const history = useHistory();

  return (
    <div className={className}>
      <div className="header-container">
        <div className="left-content">
          <img src={Logo} onClick={() => history.push("/")} />
          <p>{props.title}</p>
        </div>

        <div className="center-content">
          <div
            className={arrowLeftClassName}
            onClick={() => history.push(`/chapters/${props.previousId}`)}
          >
            <div className="arrow-left" />
          </div>
          <h1>{props.chapterNumber.toString()}</h1>
          <div
            className={arrowRightClassName}
            onClick={() => history.push(`/chapters/${props.nextId}`)}
          >
            <div className="arrow-right" />
          </div>
        </div>
      </div>
      <div className="mobile-chapter-controller">
        <div
          className={arrowLeftClassName}
          onClick={() => history.push(`/chapters/${props.previousId}`)}
        >
          <div className="arrow-left" />
        </div>
        <h1>{props.chapterNumber.toString()}</h1>
        <div
          className={arrowRightClassName}
          onClick={() => history.push(`/chapters/${props.nextId}`)}
        >
          <div className="arrow-right" />
        </div>
      </div>
    </div>
  );
};

export default ChapterDetailHeader;
