import React, { useCallback, useEffect, useRef, useState } from "react";

import "./Navbar.scss";

import { NavLink, useHistory, useRouteMatch } from "react-router-dom";
import MenuIcon from "@material-ui/icons/Menu";
import CloseIcon from "@material-ui/icons/Close";
import Logo from "../../assets/images/logo-03.png";
import useEventListener from "../../utils/useEventListener";

const Navbar: React.FC = () => {
  const matchChapterScreen = useRouteMatch("/chapters");
  const matchHomesScreen = useRouteMatch("/");
  const matchCharacterScreen = useRouteMatch("/characters");
  const matchAboutScreen = useRouteMatch("/about");

  if (
    !matchChapterScreen?.isExact &&
    !matchHomesScreen?.isExact &&
    !matchCharacterScreen?.isExact &&
    !matchAboutScreen?.isExact
  ) {
    return <div></div>;
  }

  return (
    <nav>
      <NavbarMobile />
      <div className="Navbar">
        <ul className="nav-links">
          <NavLink
            activeClassName="nav-active"
            to="/"
            className="nav-link-i"
            exact
          >
            <li>Home</li>
          </NavLink>
          <NavLink
            activeClassName="nav-active"
            className="nav-link-i"
            to="/chapters"
          >
            <li>Chapters</li>
          </NavLink>

          <NavLink
            activeClassName="nav-active"
            className="nav-link-i"
            to="/characters"
          >
            <li>Characters</li>
          </NavLink>

          <NavLink
            activeClassName="nav-active"
            className="nav-link-i"
            to="/about"
          >
            <li>About</li>
          </NavLink>
        </ul>
      </div>
    </nav>
  );
};

const NavbarMobile: React.FC = () => {
  const [open, setOpen] = useState(false);
  const history = useHistory();
  return (
    <div className="NavbarMobile">
      <img
        src={Logo}
        className="menu-logo"
        onClick={() => history.replace("/")}
      />
      <MenuIcon className="menu-icon" onClick={() => setOpen(true)} />

      {open ? (
        <div className="content">
          <ul>
            <div
              style={{ margin: "-50px 0 50px 0" }}
              className="nav-link-i"
              onClick={() => setOpen(false)}
            >
              <CloseIcon style={{ width: 30, height: 30 }} />
            </div>

            <NavLink
              activeClassName="nav-active"
              to="/"
              className="nav-link-i"
              exact
              onClick={() => setOpen(false)}
            >
              <li>Home</li>
            </NavLink>
            <NavLink
              activeClassName="nav-active"
              className="nav-link-i"
              to="/chapters"
              onClick={() => setOpen(false)}
            >
              <li>Chapters</li>
            </NavLink>

            <NavLink
              activeClassName="nav-active"
              className="nav-link-i"
              to="/characters"
              onClick={() => setOpen(false)}
            >
              <li>Characters</li>
            </NavLink>

            <NavLink
              activeClassName="nav-active"
              className="nav-link-i"
              to="/about"
              onClick={() => setOpen(false)}
            >
              <li>About</li>
            </NavLink>
          </ul>
        </div>
      ) : null}
    </div>
  );
};

export default Navbar;
