import React from "react";
import "./ChapterList.scss";

import thumbnail01 from "../../assets/images/chapters/thumbnail-01.jpg";

import ChapterItem from "./components/ChapterItem/ChapterItem";

import API_DATA from "../../services/chapter_data";

const ChapterListScreen: React.FC = () => {
  return (
    <div className="ChapterList">
      {API_DATA.map((chapter) => {
        return (
          <div className="chapter_item">
            <ChapterItem
              title={chapter.title}
              thumbnailUrl={chapter.thumbnail_url}
              description={"New Blood"}
              creationTime={new Date(chapter.creation_time)}
              id={chapter.id}
            />
          </div>
        );
      })}
    </div>
  );
};

export default ChapterListScreen;
